body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: #013161;
  color: #fff;
  padding: 0;
  margin: 0;
  max-width: 100%;
  text-align: center;
}
a {
  color: #49c7ff;
  text-decoration: none;
}
h1,
button {
  text-align: center;
}
.btn-close {
  position: absolute;
  right: 5px;
  color: #013161;
  background: #ffffff47;
  border: none;
}

.menu-cat {
  display: inline-flex;
  text-align: left;
  width: 100%;
  margin: 0 30px;
}
.btn-cat {
  position: relative;
  display: inline-block;
  text-align: left;
  height: 38px;
  width: 60px;
  min-width: 50px;
  font-size: 1.02em;
  text-transform: uppercase;
  color: #fff;
  border: #eee;
  padding: 2px 10px;
  margin: 2px 6px;
  border-radius: 10px;
  vertical-align: top;
  transition: all 0.5s ease-out;
  text-align: left;
}

.picto-cat {
  text-align: left;
}
.label-cat {
  position: absolute;
  transition: all 1s ease-out;
  top: 10px;
  margin-left: 40px;
  vertical-align: center;
}
.cat-color-2 {
  background: #6eab27;
}
.cat-color-3 {
  background: #815e58;
}

.active_cat button {
  width: 180px;
}

.content {
  width: 1200px;
  max-width: 95%;
  text-align: center;
  margin: auto;
}
.card {
  display: inline-block;
  vertical-align: top;
  background: #fff;
  color: #013161;
  transform: rotateY(90deg);
  transition-duration: 0.6s;
  padding: 10px;
  font-family: sans-serif;
  text-align: center;
  font-size: 0.95em;
  margin: 0 10px;
  width: 250px;
  border-radius: 10px;
  max-width: 20%;
  margin-bottom: 2%;
  min-height: 120px;
}
.chiffre_cle {
  font-weight: 900;
  font-size: 1.7em;
  color: #8bc34a;
}
.cat-2 .chiffre_cle {
  color: #8bc34a;
}
.cat-3 .chiffre_cle {
  color: #513431;
}

ul {
  list-style-type: none;
  padding-inline-start: 0px;
  margin-block-start: 0;
}
li {
  margin: 0;
}

.delay-1 {
  transform: rotateY(0deg);
  transition-delay: 0s;
}
.delay-2 {
  transform: rotateY(0deg);
  transition-delay: 0.5s;
}
.remove-1 {
  transform: rotateY(90deg);
}
.remove-2 {
  transform: rotateY(90deg);
  transition-delay: 0.5s;
}
p.chiffre_cle {
  margin: 0;
}

@media only screen and (max-width: 680px) {
  .card {
    width: 250px;
    max-width: 98%;
  }
  .menu-cat {
    margin: 0;
  }
}
